import { LitElement, css, html } from 'lit';
import { customElement } from 'lit/decorators.js';

@customElement('app-susunkata')
export class AppMain extends LitElement {
  static get styles() {
    return css`

    fluent-card {
      padding: 0px 18px 18px;
    }

    @media(prefers-color-scheme: light) {
      fluent-card {
        --fill-color: #edebe9;
      }
    }

    @media(prefers-color-scheme: dark) {
      fluent-card {
        --fill-color: #4e4e4e;
        color: white;
        border: none;
      }
    }

    @media (min-width: 1024px) {
      fluent-card {
        width: 54%;
      }
    }
    `;
  }

  constructor() {
    super();	
  }
  
  async firstUpdated() {
  
	console.log('ssfirstUpdated');
	if (localStorage.getItem("shareddated") === null) {
		console.log('shareddated is null');
		window.open("/sharebonus", "_top"); //relative to domain
	}else{
		var shareddated = localStorage.getItem("shareddated");
		
		const now = new Date();
		console.log(now);
		const datenow = new Date(now.getFullYear(), now.getMonth(), now.getDate());
		console.log(datenow);
		const datenowint = Date.parse(datenow.toString());
		console.log(datenowint);
		if(	shareddated!=null){
			if(parseInt(shareddated) < datenowint){
				window.open("/sharebonus", "_top"); //relative to domain
			}
		}else{
			window.open("/sharebonus", "_top"); //relative to domain
		}
	}	

  }  

  render() {
    return html`
      <app-header ?enableBack="${true}"></app-header>

      <div>
		<h2>Celenganku : Susunkata</h2>
		<fluent-card>
			<center><img alt="App Logo" src="/assets/icons/icon_512.png" style="width: 100%;height: auto;"></center>
			<p>...</p>
		</fluent-card>
		<fluent-anchor href="${(import.meta as any).env.BASE_URL}main" appearance="accent">Menu Utama</fluent-anchor>
		<!--


        <fluent-card>
          <h2>Did you know?</h2>

          <p>PWAs have access to many useful APIs in modern browsers! These
            APIs have enabled many new types of apps that can be built as PWAs, such as advanced graphics editing apps, games, apps that use machine learning and more!
          </p>

          <p>Check out <fluent-anchor href="https://docs.microsoft.com/en-us/microsoft-edge/progressive-web-apps-chromium/how-to/handle-files">these docs</fluent-anchor> to learn more about the advanced features that you can use in your PWA</p>

        </fluent-card>
		-->
      </div>
    `;
  }
}
